@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap);
/* base styles */
body {
  font-family: Poppins, sans-serif;
  margin: 0;
  font-size: 1.1em;
}
ul, li, p, h1, h2, h3, h4 {
  margin: 0;
  padding: 0;
}
ul {
  list-style-type: none;
}
.btn {
  background: none;
  border: 2px solid #1f9751;
  padding: 6px 12px;
  border-radius: 4px;
  color: #1f9751;
  font-weight: bold;
  cursor: pointer;
  font-size: 1em;
}
.btn:hover {
  background: #1f9751;
  color: #fff;
}
/* Navbar styles */
.Navbar_navbar__2oMZG {
  width: 100%;
  background: #effaf0;
  padding: 20px 10px;
  box-sizing: border-box;
}

.Navbar_navbar__2oMZG ul {
  display: flex;
  margin: 0 auto;
  max-width: 960px;
  align-items: center;
}

.Navbar_title__3Yqjq {
  margin-right: auto;
  font-weight: bold;
  letter-spacing: 1px;
  font-size: 1.2em;
}

.Navbar_navbar__2oMZG button,
.Navbar_navbar__2oMZG a {
  margin-left: 16px;
}

.Navbar_navbar__2oMZG a {
  color: #333;
  text-decoration: none;
}

.Home_container__1USVs {
  display: grid;
  grid-template-columns: 2fr 1fr;
  max-width: 960px;
  margin: 60px auto;
}
.Home_content__hhRqu {
  padding-right: 30px;
}
.Home_sidebar__59F3s {
  padding-left: 30px;
}
.Home_sidebar__59F3s form {
  padding: 20px;
  background: #1f9751;
  border-radius: 10px;
}
.Home_sidebar__59F3s input,
.Home_sidebar__59F3s select {
  display: block;
  width: 100%;
  padding: 10px;
  margin-top: 8px;
  box-sizing: border-box;
  border: 0;
  border-radius: 4px;
  color: #555;
  font-size: 1em;
}
.Home_sidebar__59F3s label {
  margin: 0 auto 20px;
  display: block;
  color: #fff;
}
.Home_sidebar__59F3s button {
  color: #fff;
  border: 2px solid #fff;
  padding: 6px 12px;
  background-color: transparent;
  font-size: 1em;
  border-radius: 4px;
  cursor: pointer;
  display: block;
  width: 100%;
}
.Home_sidebar__59F3s h3 {
  color: #1f9751;
  margin-bottom: 20px;
}
.Home_sidebar__59F3s aside {
  margin-top: 40px;
  color: #555;
}
.Home_sidebar__59F3s aside li {
  margin: 10px;
}

/* transactions */
.Home_transactions__2fqWp li {
  margin: 30px auto;
  border: 1px solid #f2f2f2;
  box-shadow: 3px 3px 5px rgba(50, 50, 50, 0.1);
  padding: 20px;
  display: flex;
  align-items: center;
  position: relative;
  overflow: hidden;
  border-left: 4px solid #1f9751;
}
.Home_transactions__2fqWp .Home_name__2JLnu {
  color: #777;
  font-size: 1.4em;
}
.Home_transactions__2fqWp .Home_amount__TNBQj {
