  margin-right: 40px;
  color: #777;
  font-weight: bold;
  font-size: 1.6em;
}
.Home_transactions__2fqWp button {
  position: absolute;
  top: 0;
  right: 0;
  background: #ddd;
  color: #777;
  border: none;
  padding: 12px 8px;
  text-align: center;
  line-height: 0;
